
.footer {
    width: 100%;
    background-color: #4157A2;
    text-align: center;
}

.footer__link {
    text-decoration: none;
    color: white;
    font-size: 30px;
    transition: opacity .3s linear;
}

.footer__link p {
    display: block;
    margin: 0;
    padding: 20px 0;
}
.footer__link:hover {
    opacity: .6;
}
