.header{
    display: flex;
    background-color: #4157A2;
    justify-content: center;
    font-size: 30px;
}

.nav {
    display: flex;
    gap: 54px;
    justify-content: space-between;
    list-style: none;
    padding: 24px 0;
}

.link {
    color: white;
    text-decoration: none;
    transition: opacity .3s linear;
}


.link:hover {
    opacity: .6;
}

